.gallery {
  &__item {
    display: flex;
    justify-content: space-between;
    margin-bottom: 24px;
  }

  &__pic {
    width: calc(50% - 5px);
  }

  &__item--left-wide &__pic:first-child,
  &__item--right-wide &__pic:last-child {
    width: calc(67% - 5px);
  }

  &__item--left-wide &__pic:last-child,
  &__item--right-wide &__pic:first-child {
    width: calc(33% - 5px);
  }

  &__img {
    display: block;
    border-radius: 8px;
    height: 256px;
    width: 100%;
    object-fit: cover;
    object-position: center;
  }

  &__link {
    margin-bottom: 40px;
    display: block;

    color: inherit;

    & mark {
      color: #0075ff;
      background-color: inherit;
      font-weight: 700;
    }
  }

  &__button {
    color: #0075ff;
    border: 2px solid #0075ff;
    background-color: inherit;
    box-shadow: none;

    &:hover {
      color: #fff;
      background-color: #8ab8ee;
    }
  }

  @media screen and (max-width: 768px) {
    &__item {
      margin-bottom: 16px;
    }

    &__img {
      height: 153px;
    }

    &__item--left-wide &__pic:first-child,
    &__item--right-wide &__pic:last-child,
    &__item--left-wide &__pic:last-child,
    &__item--right-wide &__pic:first-child {
      width: calc(50% - 5px);
    }

    &__link {
      margin-bottom: 32px;
    }
    
    &__button {
      width: 100%;
    }
  }
}
